/**
 * A hexadecimal color value (RGB or RGBA) that starts with a '#'
 * @example #6686FF or #6686FFFF
 * @public
 */
export type HexColorString = string;

/**
 * A color definition for the custom color palette.
 * The RGB and CMYK components must all be specified in the range [0-1].
 * @public
 */
export type PaletteColor = HexColorString | RGBColor | RGBAColor | SpotColor;

/**
 * A type alias for all color types supported by the engine.
 * @public
 */
export type Color = RGBAColor | CMYKColor | SpotColor;

/**
 * @public
 */
export type ColorSpace = 'sRGB' | 'CMYK' | 'SpotColor';

/**
 * All components between 0 and 1
 * @public
 */
export interface RGBColor {
  /** Red */
  r: number;
  /** Green */
  g: number;
  /** Blue */
  b: number;
}

/**
 * All components between 0 and 1
 * @public
 */
export interface HSLColor {
  /** Hue */
  h: number;
  /** Saturation */
  s: number;
  /** Lightness */
  l: number;
}

/**
 * All components between 0 and 1
 * @public
 */
export interface HSVColor {
  /** Hue */
  h: number;
  /** Saturation */
  s: number;
  /** Value */
  v: number;
}

export interface ColorNameObject {
  name: string;
  hex: string;
  rgba: RGBAColor;
}

/**
 * All components between 0 and 1
 * @public
 */
export interface RGBAColor {
  /** Red */
  r: number;
  /** Green */
  g: number;
  /** Blue */
  b: number;
  /** Alpha */
  a: number;
}

/**
 * All components between 0 and 1
 * @public
 */
export interface CMYKColor {
  /** Cyan */
  c: number;
  /** Magenta */
  m: number;
  /** Yellow */
  y: number;
  /** Black */
  k: number;
  /** The tint factor */
  tint: number;
}

/** @public */
export interface SpotColor {
  name: string;
  tint: number;
  externalReference: string;
}

/**
 * Internal color representation
 * @internal
 */
export interface ColorInternal {
  colorSpace: ColorInternal.ColorSpace;
  components: {
    x: number;
    y: number;
    z: number;
    w: number;
  };
  spotColorName: string;
  tint: number;
  /** The external reference of a spot color, e.g. the name of formula guide it comes from. */
  externalReference: string;
}

/** @public */
export interface GradientColorStop {
  /** A color value within a gradient. */
  color: Color;
  /** The relative position of the color within the gradient in the range [0, 1]. */
  stop: number;
}

/**
 * Type guard for {@link RGBAColor}
 * @public
 */
export function isRGBAColor(color: Color): color is RGBAColor {
  return (
    'r' in color &&
    'a' in color &&
    color.r !== undefined &&
    color.a !== undefined
  );
}

/**
 * Type guard for {@link SpotColor}
 * @public
 */
export function isSpotColor(color: Color): color is SpotColor {
  return 'name' in color && color.name !== undefined;
}

/**
 * Type guard for {@link CMYKColor}
 * @public
 */
export function isCMYKColor(color: Color): color is CMYKColor {
  return (
    'c' in color &&
    'm' in color &&
    'y' in color &&
    'k' in color &&
    color.c !== undefined &&
    color.m !== undefined &&
    color.y !== undefined &&
    color.k !== undefined
  );
}

/** @internal */
export namespace ColorInternal {
  export enum ColorSpace {
    sRGB = 0,
    CMYK = 1,
    SpotColor = 2
  }

  export function toColor(color: ColorInternal): Color {
    switch (color.colorSpace) {
      case ColorSpace.sRGB:
        return {
          r: color.components.x,
          g: color.components.y,
          b: color.components.z,
          a: color.components.w
        };
      case ColorSpace.SpotColor: {
        return {
          name: color.spotColorName,
          tint: color.tint,
          externalReference: color.externalReference
        };
      }
      case ColorSpace.CMYK:
        return {
          c: color.components.x,
          m: color.components.y,
          y: color.components.z,
          k: color.components.w,
          tint: color.tint
        };
      default:
        throw new Error('Unknown color space!');
    }
  }

  export function fromColor(color: Color): ColorInternal {
    if (isRGBAColor(color)) {
      return {
        colorSpace: ColorSpace.sRGB,
        components: { x: color.r, y: color.g, z: color.b, w: color.a },
        spotColorName: '',
        tint: 1,
        externalReference: ''
      };
    } else if (isCMYKColor(color)) {
      return {
        colorSpace: ColorSpace.CMYK,
        components: { x: color.c, y: color.m, z: color.y, w: color.k },
        spotColorName: '',
        tint: color.tint,
        externalReference: ''
      };
    } else if (isSpotColor(color)) {
      return {
        colorSpace: ColorSpace.SpotColor,
        components: { x: 0, y: 0, z: 0, w: 0 },
        spotColorName: color.name,
        tint: color.tint,
        externalReference: color.externalReference
      };
    }
    throw new Error('Unknown color space!');
  }
}
