import type { Configuration } from '../configuration';
import { LogLevel } from '../types/Logger';
import type MimeType from '../types/MimeType';
import type { TrackingMetadata } from '../types/engine';
import type { Scope } from '../types/scope';
import type { Buffer } from '../ubq/ubq_binding';
import type { ExportOptions } from './exportVideo';

/** A version of the Configuration type with non-transferable data removed */
export type WorkerConfiguration = Omit<Configuration, 'logger'> & {
  logger?: undefined;
};

export type SetVisibility = {
  msg: 'setVisibility';
  visible: boolean;
};

export function setVisibility(visible: boolean): SetVisibility {
  return { msg: 'setVisibility', visible };
}

/**
 * Data needed for setting up the engine in the worker that is _not_ covered by the config
 * @internal
 * */
export type WorkerOptions = {
  trackingMetadata?: TrackingMetadata;
  scopes?: { [scope in Scope]: 'Allow' | 'Deny' | 'Defer' };
  buffers?: Buffer[];
  uriResolver?: string;
};

namespace ExportVideo {
  export type Start = {
    msg: 'exportVideo';
    config: WorkerConfiguration;
    engineSettings: Map<string, any>;
    sceneString: string;
    exportOptions: ExportOptions;
    inactivityTimeout: number;
    workerOptions: WorkerOptions;
  };

  export type Abort = {
    msg: 'exportVideoAbort';
  };

  export type Finished = {
    msg: 'exportVideoFinished';
    data: Uint8Array;
    mimeType: MimeType;
  };

  export type Error = {
    msg: 'exportVideoError';
    error: unknown;
  };

  export type Progress = {
    msg: 'exportVideoProgress';
    totalFrames: number;
    renderedFrames: number;
    encodedFrames: number;
  };

  export type Log = {
    msg: 'exportLog';
    logLevel?: LogLevel;
    message: string;
  };

  export type Command = Start | Abort;

  export type Reponse = Finished | Error | Progress | Log;

  export function start(args: Omit<Start, 'msg'>): Start {
    return { ...args, msg: 'exportVideo' };
  }
  export function abort(): Abort {
    return { msg: 'exportVideoAbort' };
  }
  export function finished(args: Omit<Finished, 'msg'>): Finished {
    return { ...args, msg: 'exportVideoFinished' };
  }
  export function error(args: Omit<Error, 'msg'>): Error {
    return { ...args, msg: 'exportVideoError' };
  }
  export function progress(args: Omit<Progress, 'msg'>): Progress {
    return { ...args, msg: 'exportVideoProgress' };
  }
  export function log(args: Omit<Log, 'msg'>): Log {
    return { ...args, msg: 'exportLog' };
  }
}

type WorkerCommand = ExportVideo.Command | SetVisibility;

type WorkerResponse = ExportVideo.Reponse;

export { ExportVideo };
export type { WorkerCommand, WorkerResponse };
