import assert, { string } from './assert';
import { getter, setter } from './reactor/decorate';
import type { UBQ } from './ubq/ubq_binding';
import { unpackResult } from './ubq/ubq_result';
import cppVectorToArray from './utils/cppVectorToArray';

/**
 * @public
 */
export default class VariableAPI {
  /** @internal */
  #ubq: UBQ;

  /** @internal */
  constructor(ubq: UBQ) {
    this.#ubq = ubq;
  }

  /**
   * Get all text variables currently stored in the engine.
   * @returns Return a list of variable names
   */
  @getter
  findAll(): string[] {
    return cppVectorToArray(this.#ubq.findAllVariables());
  }

  /**
   * Set a text variable.
   * @param key - The variable's key.
   * @param value - The text to replace the variable with.
   */
  @setter
  setString(key: string, value: string): void {
    assert('key', key, string());
    assert('value', value, string());
    return unpackResult(this.#ubq.setVariableString(key, value));
  }

  /**
   * Set a text variable.
   * @param key - The variable's key.
   * @returns  The text value of the variable.
   */
  @getter
  getString(key: string): string {
    assert('key', key, string());
    return unpackResult(this.#ubq.getVariableString(key));
  }

  /**
   * Destroy a text variable.
   * @param key - The variable's key.
   */
  @setter
  remove(key: string): void {
    assert('key', key, string());
    return unpackResult(this.#ubq.removeVariable(key));
  }
}
