import type { Configuration } from './configuration';
import type { UBQ } from './ubq/ubq_binding';
import { unpackResult } from './ubq/ubq_result';

const licenseEndpoint =
  IMGLY_NODE_ENV === 'development'
    ? 'https://api.staging.img.ly/activate'
    : 'https://api.img.ly/activate';

type LicenseReponse =
  | { status: 'valid'; license: string }
  | { status: 'invalid' | 'expired' };

/**
 * Takes a config object and returns a promise that resolves to a license key.
 *
 * It will take the license from `config.license` and depending on its length,
 * either return it as a license key, or interpret it as an api key and use that
 * to fetch a license from the license endpoint.
 *
 * If `config.license` is missing, it will **synchronously** throw an error.
 *
 * There is no guarantee that the returned license key is valid.
 * That needs to be further used with `ubq.unlockWithLicense()`.
 * @internal
 */
export function downloadAndCheckLicense(
  config: Configuration,
  fetchImpl: typeof window.fetch = fetch
): Promise<string> {
  // The function is not async, because we want to throw this error synchronously.
  if (!config.license) {
    throw new Error('Missing license key in config');
  }

  const license =
    config.license.length < 128
      ? fetchImpl(licenseEndpoint, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            apiKey: config.license,
            userId: config.userId
          })
        })
          .then((response) => {
            if (response.ok) {
              return response.json();
            } else {
              throw new Error(
                `Unfortunately we are experiencing a server down time and your License key cannot be validated. We're already working on a fix and will restore service soon. Should services not restore within the hour, kindly get in touch with our support team.`
              );
            }
          })
          .then((data: LicenseReponse) => {
            if (data.status === 'valid') {
              return data.license;
            } else if (data.status === 'expired') {
              throw new Error(
                'Thanks for using IMG.LY for creative editing. Please note that your license file or commercial use is expired. Please get in touch with our sales team to discuss extension of your commercial license.'
              );
            } else {
              throw new Error(
                'The License Key (API Key) you are using to access the IMG.LY SDK is invalid. Please ensure that you are using the license key tied to your subscription and get in touch with our support team.'
              );
            }
          })
      : Promise.resolve(config.license);

  return license;
}

/**
 * Unlocks a UBQ instance with a given license key.
 * @param ubq - The UBQ instance to unlock.
 * @param license - The license key to unlock the UBQ instance with. Don't pass and API key
 * @internal
 */
export function unlockUbq(ubq: UBQ, license: string): void {
  if (!license) throw new Error('Missing license key');
  unpackResult(ubq.unlockWithLicense(license));
}
