import { makeValueChannel } from '../streams/Channel';
import type Logger from '../types/Logger';
import MimeType from '../types/MimeType';
import exportVideo from './exportVideo';
import {
  ExportVideo,
  type WorkerCommand,
  type WorkerResponse
} from './workerTypes';

declare const WorkerGlobalScope: unknown;

declare const self: {
  onmessage?: (ev: MessageEvent<WorkerCommand>) => void;
  postMessage(message: WorkerResponse): void;
};

/*
 * This file, imported from index.js lets us use the whole @cesdk/engine module
 * as the source for a web worker, not requiring a complicated separate module.
 */

if (typeof WorkerGlobalScope !== 'undefined') {
  const visibility = makeValueChannel(() => true);
  const exportVideoAbortController = new AbortController();

  self.onmessage = async ({ data }) => {
    const logger: Logger = (message, logLevel) => {
      self.postMessage(ExportVideo.log({ logLevel, message }));
    };

    if (data.msg === 'setVisibility') {
      visibility.update(data.visible);
    } else if (data.msg === 'exportVideo') {
      exportVideo(
        {
          ...data.config,
          logger,
          abortSignal: exportVideoAbortController.signal
        },
        data.engineSettings,
        data.sceneString,
        data.exportOptions,
        data.workerOptions,
        {
          onProgress: (renderedFrames, encodedFrames, totalFrames) => {
            self.postMessage(
              ExportVideo.progress({
                renderedFrames,
                encodedFrames,
                totalFrames
              })
            );
          },
          visibility,
          inactivityTimeout: data.inactivityTimeout
        }
      ).then(
        (videoData) => {
          self.postMessage(
            ExportVideo.finished({ data: videoData, mimeType: MimeType.Mp4 })
          );
        },
        (error) => {
          self.postMessage(ExportVideo.error({ error }));
        }
      );
    } else if (data.msg === 'exportVideoAbort') {
      exportVideoAbortController.abort();
    }
  };
}
