// https://stackoverflow.com/a/32598826
export const canUseDOM = !!(
  typeof window !== 'undefined' &&
  window.document &&
  window.document.createElement
);

const userAgent = (canUseDOM && navigator && navigator.userAgent) || '';

export const isEdge = userAgent.indexOf('Edge') !== -1;

export const isIE = !!userAgent.match(/msie|trident/i);

// https://developer.mozilla.org/en-US/docs/Web/HTTP/Browser_detection_using_the_user_agent#making_the_best_of_user_agent_sniffing
export const isMobileWebKit =
  /\b(iPad|iPhone|iPod)\b/.test(userAgent) &&
  /WebKit/.test(userAgent) &&
  !/Edge/.test(userAgent) &&
  (typeof window === 'undefined' || !('MSStream' in window));
