// This is very internal, very close to C++, like UBQResult
/** @internal */
export interface Vector<T> extends EmscriptenClassHandle {
  size: () => number;
  get: (index: number) => T;
}

/**
 * A numerical identifier for a design block
 * @public
 */
export type DesignBlockId = number;

/**
 * A numerical identifier for a history stack
 * @public
 */
export type HistoryId = number;

/** @internal */
export interface NumberRange {
  start: number;
  end: number;
}

// Check for duplicates
/** @public */
export interface Vec2 {
  x: number;
  y: number;
}

// Check for duplicates
/** @public */
export interface Vec3 {
  x: number;
  y: number;
  z: number;
}

// Check for duplicates
/** @public */
export interface Size2 {
  width: number;
  height: number;
}

/**
 * An open range.
 * @public
 * */
export interface Range {
  /** The starting value of the range */
  from: number;
  /** The non-inclusive ending value of the range */
  to: number;
}

/** @internal */
export interface ClampArgs {
  horizontalPaddingInPixels: number;
  verticalPaddingInPixels: number;
}

/** @internal */
export interface ClampCameraCommandResult {
  scrollPercentage: Vec2;
  sceneSize: Vec2;
}

// These 3 things go to their own file

/** @internal */
export interface EmscriptenClassHandle {
  isAliasOf(other: unknown): boolean;
  clone(): this;
  delete(): void;
  isDeleted(): boolean;
  // This function exists, but I think it won' delete until we set
  // a delayFunction in the CESDK_JS Module:
  // deleteLater(): this;
}

/**
 * @internal
 */
export interface UBQError extends EmscriptenClassHandle {
  message(): string;
  publicMessage(): string;
}

/**
 * Result object that binds the C++ `ResultOrError<T, Error>` object. Acessing `value()` for an
 * invalid object will cause an exception in C++.
 * @internal
 */
export interface UBQResult<T> extends EmscriptenClassHandle {
  value(): T;
  error(): UBQError;
  isValid(): boolean;
  valueOr(t: T): T;
}

// These are used for mouse/keyboard interaction with the engine
// Should probably go to its own file
/** @internal */
export enum MouseButton {
  Left = 0,
  Right = 1,
  Middle = 2,
  Unknown = 255
}

/**
 * Describes the unit of the mouse wheel delta.
 * @internal
 */
export enum MouseWheelDeltaMode {
  Pixel = 0,
  Line = 1,
  Page = 2
}

/** @internal */
export enum ButtonState {
  Up = 0,
  Down = 1
}

/** @internal */
export interface MouseButtonEvent {
  button: MouseButton;
  state: ButtonState;
  position: Vec2;
  shiftIsHeld: boolean;
  isTouch: boolean;
  timestamp: number;
}

/** @internal */
export interface MouseMoveEvent {
  position: Vec2;
  isTouch: boolean;
  timestamp: number;
}

/** @internal */
export interface MouseWheelEvent {
  position: Vec2;
  timestamp: number;
  deltaX: number;
  deltaY: number;
  deltaMode: MouseWheelDeltaMode;
  shiftIsHeld: boolean;
  /**
   * Ctrl is used for zooming.
   * Browsers will emit wheel events with ctrl held when using a two-finger
   * pinch, Meta (CMD) is also considered for pro users who zoom with CMD+Wheel
   */
  ctrlOrMetaIsHeld: boolean;
}

/** @internal */
export enum KeyboardKey {
  Key0 = 0,
  Key1 = 1,
  Key2 = 2,
  Key3 = 3,
  Key4 = 4,
  Key5 = 5,
  Key6 = 6,
  Key7 = 7,
  Key8 = 8,
  Key9 = 9,
  A = 10,
  B = 11,
  C = 12,
  D = 13,
  E = 14,
  F = 15,
  G = 16,
  H = 17,
  I = 18,
  J = 19,
  K = 20,
  L = 21,
  M = 22,
  N = 23,
  O = 24,
  P = 25,
  Q = 26,
  R = 27,
  S = 28,
  T = 29,
  U = 30,
  V = 31,
  W = 32,
  X = 33,
  Y = 34,
  Z = 35,
  ArrowLeft = 36,
  ArrowRight = 37,
  ArrowUp = 38,
  ArrowDown = 39,
  Backspace = 40,
  Enter = 41,
  Space = 42,
  Escape = 43,
  Delete = 44,
  // Modifier Keys
  Control,
  Option,
  Shift,
  Unknown = 255
}

/** @internal */
export interface KeyboardKeyEvent {
  key: KeyboardKey;
  characters: string;
  shiftIsHeld: boolean;
  commandIsHeld: boolean;
  optionIsHeld: boolean;
  timestamp: number;
}

/** @internal */
export enum NotificationType {
  Information,
  Warning,
  Error
}

/**
 * The scene layout determines how the layout stack should layout its pages.
 * @public
 */
export type SceneLayout =
  | 'Free'
  | 'VerticalStack'
  | 'HorizontalStack'
  | 'DepthStack';

/**
 * The old numeric enum used to represent scene layout
 * @internal
 * @deprecated This is only used internally between Ubique and the LegacyAPI
 */
export enum SceneLayoutNumeric {
  Free,
  VerticalStack,
  HorizontalStack,
  DepthStack
}

/**
 * The mode of the scene defines how the editor and playback should behave.
 * @public
 */
export type SceneMode = 'Design' | 'Video';

/**
 * The axis(es) for which to auto-fit.
 * @public
 */
export type ZoomAutoFitAxis = 'Horizontal' | 'Vertical' | 'Both';

/**
 * The unit type in which the page values (size, distances, etc.) are defined.
 * @public
 */
export type DesignUnit = 'Pixel' | 'Millimeter' | 'Inch';

/**
 * The horizontal text alignment options.
 * @public
 */
export type HorizontalTextAlignment = 'Left' | 'Right' | 'Center';

/**
 * The vertical text alignment options.
 * @public
 */
export type VerticalTextAlignment = 'Top' | 'Bottom' | 'Center';

/** @internal */
export enum GestureState {
  Began = 0,
  Changed = 1,
  Ended = 2
}

/** @internal */
export interface EngineTouchEvent {
  /** Nanoseconds */
  timestamp: number;
  state: GestureState;
  touchPoints: Vec2[];
}

/** @internal */
export interface TouchRotateEvent extends EngineTouchEvent {
  rotation: number;
}

/** @internal */
export interface TouchPanEvent extends EngineTouchEvent {
  panDistance: Vec2;
}

/** @internal */
export interface TouchPinchEvent extends EngineTouchEvent {
  scale: number;
}

/** @internal */
export interface TrackingMetadata {
  apiKey: string;
  userId: string;
  deviceId: string;
  sessionId: string;
  endpoint: string;
}

/** @public */
export type EditMode =
  | 'Transform'
  | 'Crop'
  | 'Text'
  | 'Playback'
  | 'Trim'
  | (string & {});
