import {
  array,
  boolean,
  define,
  enums,
  integer,
  max,
  min,
  nonempty,
  number,
  object,
  string,
  Struct,
  tuple,
  type,
  validate
} from 'superstruct';
import { type CutoutOperation } from './types/cutout';
import type { DesignUnit, SceneLayout, ZoomAutoFitAxis } from './types/engine';

export {
  array,
  boolean,
  enums,
  integer,
  max,
  min,
  nonempty,
  number,
  object,
  string
};

export default function assert<T = unknown, S = unknown>(
  name: string,
  value: T,
  struct: Struct<T, S>
): void {
  const [err] = validate<T, S>(value, struct);
  if (err) {
    err.message = `Error in argument '${name}': ${err.message}`;
    throw err;
  }
}

type MimeType =
  | 'image/png'
  | 'image/jpeg'
  | 'image/webp'
  | 'image/x-tga'
  | 'audio/wav'
  | 'video/mp4'
  | 'video/quicktime'
  | 'application/octet-stream'
  | 'application/pdf'
  | 'application/zip';
export function mimeType() {
  const r =
    /image\/(png|jpeg|webp|x-tga)|audio\/(wav)|video\/(mp4|quicktime)|application\/octet-stream|application\/pdf/;

  return define<MimeType>('MimeType', (value) => {
    if (typeof value === 'string' && r.test(value)) {
      return true;
    } else {
      return {
        message: `expected one of "image/png", "image/jpeg", "image/webp", "image/x-tga", "video/mp4", "video/quicktime", "application/pdf" or "application/octet-stream", but got "${value}"`
      };
    }
  });
}

export function sceneLayoutShape() {
  const options: SceneLayout[] = [
    'Free',
    'VerticalStack',
    'HorizontalStack',
    'DepthStack'
  ];
  return define<SceneLayout>('SceneLayout', (value) => {
    if (typeof value === 'string' && (options as string[]).includes(value)) {
      return true;
    } else {
      return {
        message: `expected one of ${options
          .map((o) => `"${o}"`)
          .join(', ')}, but got "${value}"`
      };
    }
  });
}

